.answer-input-area {
  position: absolute;
  bottom: 0px;
  display: flex;
  align-items: center;
  background-color: white;
  border-radius: 2px;
  width: 100%;
  margin-top: 32px;
  max-width: 480px;
}

.answer-button {
  background-color: hsl(139deg 27% 49%);
  color: #fff;
  border-radius: 4px;
  height: 40px;
  padding: 0 8px;
  cursor: pointer;
  font-size: 16px;
  margin: 4px;
  border: 0;
}

.answer-input {
  flex: 1;
  border: 0;
  border-radius: 4px;
  outline: none;
  padding: 8px;
  box-sizing: border-box;
  transition: 0.3s;
  /* height: 32px; */
  font-size: 16px;
}

.change-resolutions {
  background-color: rgb(211, 175, 56);
}

@media only screen and (max-device-width: 375px) {
  .answer-button {
    font-size: 8px;
    height: 24px;
  }

  .answer-input {
    font-size: 8px;
  }
}

@media only screen and (min-device-width: 376px) and (max-device-width: 420px) {
  .answer-button {
    font-size: 12px;
    height: 36px;
  }

  .answer-input {
    font-size: 12px;
  }
}
