}

.answer {
  background-color: #393939;
  margin-top: 16px;
  border-radius: 2px;
  height: 32px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  box-shadow: 0 1px 2px hsla(0, 0%, 0%, 0.1), 0 1px 4px hsla(0, 0%, 0%, 0.1),
    0 2px 8px hsla(0, 0%, 0%, 0.1);
}

.wrong {
  background-color: #de585f;
}

.pass {
  background-color: rgb(211, 175, 56);
}

.success {
  background-color: hsl(139deg 27% 49%);
}

@media only screen and (max-device-width: 375px) {
  .answer {
    margin-top: 4px;
    height: 16px;
  }
}

@media only screen and (min-device-width: 376px) and (max-device-width: 420px) {
  .answer {
    margin-top: 8px;
    height: 32px;
  }
}
