.game-stats-description {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.daily-question-answer {
  text-align: center;
  font-size: 16px;
}

.wide {
  width: 120px;
}

.timer {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 50%;
}

.share-area {
  width: 50%;
}

.answer-img {
  width: 235px;
  height: 350px;
}

.close {
  cursor: pointer;
  position: absolute;
  right: 0%;
  padding: 12px 16px;
  transform: translate(0%, -50%);
}

@media only screen and (max-device-width: 375px) {
  .answer-img {
    width: 135px;
    height: 200px;
  }

  .daily-question-answer {
    font-size: 8px;
  }
}

@media only screen and (min-device-width: 376px) and (max-device-width: 420px) {
  .answer-img {
    width: 135px;
    height: 200px;
  }

  .daily-question-answer {
    font-size: 12px;
  }
}
