.daily-question {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 16px;
}

.img-canvas {
  width: 235px;
  height: 350px;
}

@media only screen and (max-device-width: 375px) {
  .img-canvas {
    width: 150px;
    height: 220px;
  }
}

@media only screen and (min-device-width: 376px) and (max-device-width: 420px) {
  .img-canvas {
    width: 150px;
    height: 220px;
  }
}
